
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
components: {
XHeader,
Group,
Cell
},
data () {
return {
}
},
methods: {
goBack () {
this.$util.getMachine(4)
},
toAfterSaleList (i) {
this.$router.push({
path: '/afterSaleList',
query: {
index: i
}
})
},
toAfterSaleBatch () {
this.$router.push({
path: '/afterSaleBatch'
})
}
},
created () {
}
}



